// Модуль генерирует HTML-структуру элементов.

Element.prototype.addClass = function (newClass) {
  this.classList.add(newClass);
  return this;
};

function createNode(tag) {
  return document.createElement(tag);
}

// Создание html-структуры элемента из списка задач
function createListItem() {
  const partialName = 'taskfield__';
  const taskItem = createNode('li').addClass(partialName + 'taskItem');
  const itemTaskField = createNode('label').addClass(partialName + 'itemTaskField');
  const itemCheckbox = createNode('input').addClass(partialName + 'itemCheckbox');
  const itemText = createNode('span').addClass(partialName + 'itemText');
  const itemOptions = createNode('div').addClass(partialName + 'itemOptions');
  const itemDate = createNode('span').addClass(partialName + 'itemDate');
  const itemEditButton = createNode('a').addClass(partialName + 'itemEditButton');
  const itemDeleteButton = createNode('a').addClass(partialName + 'itemDeleteButton');

  itemCheckbox.setAttribute('type', 'checkbox');

  itemTaskField.appendChild(itemCheckbox);
  itemTaskField.appendChild(itemText);
  itemOptions.appendChild(itemDate);
  itemOptions.appendChild(itemEditButton);
  itemOptions.appendChild(itemDeleteButton);
  taskItem.appendChild(itemTaskField);
  taskItem.appendChild(itemOptions);

  return taskItem;
}

// Создание всплывающего окна с формой для исправления
function createTooltip() {
  const partialName = 'taskfield__';

  const container = createNode('div').addClass(partialName + 'tooltip');
  const inputField = createNode('input').addClass(partialName + 'tooltipInputField');
  const changeButton = createNode('input').addClass(partialName + 'tooltipChangeButton');
  const closeButton = createNode('a').addClass(partialName + 'tooltipCloseButton');

  inputField.setAttribute('type', 'text');
  changeButton.setAttribute('type', 'submit');

  container.appendChild(inputField);
  container.appendChild(changeButton);
  container.appendChild(closeButton);

  return container;
}

export {createListItem, createTooltip};